/* stylelint-disable no-descending-specificity */
.van-image {
  position: relative;
  display: inline-block;
}
.van-image--round {
  overflow: hidden;
  border-radius: 50%;
}
.van-image--round .van-image__img {
  border-radius: inherit;
}
.van-image__img,
.van-image__error,
.van-image__loading {
  display: block;
  width: 100%;
  height: 100%;
}
.van-image__error,
.van-image__loading {
  position: absolute;
  top: 0;
  left: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  color: #969799;
  color: var(--image-placeholder-text-color, #969799);
  font-size: 0.59733rem;
  font-size: var(--image-placeholder-font-size, 0.59733rem);
  background-color: #f7f8fa;
  background-color: var(--image-placeholder-background-color, #f7f8fa);
}
.van-image__loading-icon {
  color: #dcdee0;
  color: var(--image-loading-icon-color, #dcdee0);
  font-size: 1.36533rem !important;
  font-size: var(--image-loading-icon-size, 1.36533rem) !important;
}
.van-image__error-icon {
  color: #dcdee0;
  color: var(--image-error-icon-color, #dcdee0);
  font-size: 1.36533rem !important;
  font-size: var(--image-error-icon-size, 1.36533rem) !important;
}
.image-class .taro-img__mode-aspectfit {
  width: 100%;
  height: 100%;
  max-width: none !important;
  max-height: none !important;
  object-fit: contain;
}
.image-class .taro-img__mode-center {
  object-fit: none;
  display: block;
  width: 100%;
  height: 100%;
}
.image-class .taro-img__mode-heightfix {
  height: 100%;
}
